@import url("https://fonts.thu.fail/css2?family=Noto+Sans+SC:wght@100;300;400;500;700;900&family=Noto+Serif+SC:wght@200;300;400;500;600;700;900&family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&family=Source+Serif+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap");
.success {
  color: green;
}

.failed {
  color: red;
}

.syncing {
  color: blue;
}

.unknown {
  color: gray;
}

.paused {
  color: gray;
}

.sidebar {
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  width: 100px;
  background-color: #f1f1f1;
  position: fixed;
  height: 100%;
  overflow: auto;
}

/* Sidebar links */
.sidebar h2 {
  display: block;
  color: black;
  text-decoration: none;
  margin: 0;
  padding: 25px;
}

/* Active/current link */
.sidebar h2.active {
  background-color: #15658a;
  color: white;
}

/* Links on mouse-over */
.sidebar h2:hover:not(.active) {
  background-color: #555;
  color: white;
}

/* Page content. The value of the margin-left property should match the value of the sidebar's width property */
main {
  margin-left: 100px;
}

.search {
  margin-left: 100px;
}

/* On screens that are less than 700px wide, make the sidebar into a topbar */
@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }

  .sidebar h2 {
    float: left;
  }

  main {
    margin-left: 0;
  }
}
/* On screens that are less than 400px, display the bar vertically, instead of horizontally */
@media screen and (max-width: 400px) {
  .sidebar h2 {
    text-align: center;
    float: none;
  }
}
body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", "Noto Sans SC", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "PingFang SC", "Microsoft YaHei", sans-serif;
}

a {
  text-decoration: underline;
  color: #15658a;
  filter: grayscale(0.5);
  transition: filter 0.2s ease;
}
a:hover {
  filter: none;
}

h2 {
  font-family: "Source Code Pro", source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  margin: 10px 0 5px 0;
  font-size: 24px;
}

h3 {
  margin: 6px 0 3px 0;
  font-size: 18px;
  display: flex;
  align-items: center;
}
h3 .material-icons {
  font-size: 18px;
}

table {
  white-space: nowrap;
}

td:last-child {
  width: 100%;
}

#app {
  padding: 40px 0;
  margin: 0 auto;
}

.heading {
  position: relative;
}
.heading .material-icons {
  position: absolute;
  right: calc(100% + 10px);
  top: 0;
  bottom: 0;
  align-items: center;
  cursor: pointer;
  display: none;
}

.summary {
  text-align: right;
}

.filtered {
  display: none;
}

.collapsed {
  display: none;
}

.group {
  padding: 0 80px;
}
.group:hover .heading .material-icons {
  display: inline-flex;
}

.desc {
  padding: 10px 20px;
  margin-top: 10px;
  border-left: rgba(21, 101, 138, 0.2) 10px solid;
}
.desc.missing {
  color: rgba(0, 0, 0, 0.38);
  font-style: italic;
  filter: grayscale(1);
}

.upstream, .status {
  font-size: 15px;
  line-height: 18px;
  display: flex;
  align-items: center;
}
.upstream .material-icons, .status .material-icons {
  font-size: 15px;
  line-height: 18px;
  width: 30px;
  color: rgba(0, 0, 0, 0.38);
}

.help {
  margin-left: 10px;
  display: flex;
  align-items: center;
}

main {
  padding-top: 100px;
}

.search {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  background: rgba(197, 217, 226, 0.8);
  backdrop-filter: blur(4px);
  z-index: 100;
  border-top: #15658a 5px solid;
}
.search input {
  font-family: "Source Code Pro", source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  line-height: 100px;
  font-size: 36px;
  width: 100%;
  height: 100%;
  border: none;
  outline: 0;
  padding: 0;
  background: transparent;
  padding: 0 20px 0 64px;
}
.search .material-icons {
  position: absolute;
  left: 20px;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  font-size: 32px;
  color: black;
  opacity: 1;
  transition: opacity 0.2s ease;
}
.search input[value=""] + .material-icons {
  opacity: 0.38;
}

.mirrors {
  column-count: auto;
  column-width: 600px;
  column-rule-style: solid;
}